.nav {
  height: 80px;
  background-color: rgb(40, 40, 40);
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: 22px;
  margin: auto 0;
  vertical-align: middle;

  .item {
    height: auto;
    margin: auto 0;

    a {
      margin: 0 20px;
      color: white;
      text-decoration: none;
    }

    a.active {
      color: gold;
    }
  }
}

@media (min-width: 768px) {
  .nav {
    font-size: 36px;

    .item {
      a {
        margin: 0 30px;
      }
    }
  }
}


@media (min-width: 1280px) {
  .nav {
    font-size: 46px;
  }
}

