.wrapper {
  margin-top: 70px;
  text-align: center;

  h1 {
    font-size: 36px;
  }

  p {
    text-align: justify;
    margin: 0 auto;
    width: 300px;
    font-size: 18px;
  }

  .social_wrapper {
    margin-top: 80px;

    a {
      margin: 0 25px;
      text-decoration: none;
      color: #282828;
    }
  }
}

@media (min-width: 768px) {
  .wrapper {

    p {
      width: 400px;
      font-size: 24px;
    }

    .social_wrapper {
      margin-top: 50px;
    }
  }
}